@import url('https://fonts.googleapis.com/css2?family=Bevan:ital@0;1&display=swap');
body {
  font-family: 'Bevan', cursive;
}
.App {
  --border-radius: 22px;
  margin: auto;
  margin-top: 10%;
  width: 400px;
  height : fit-content;
  padding: 12px;
  font-size: 35px;
  text-transform: uppercase;
  text-align: center;
  vertical-align: middle;
  background-color: rgb(195, 207, 233);
  border-radius: 32px;
}

.contariner {
  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-template-rows: 1fr 1fr 1fr;
  row-gap: 15px;
  column-gap: 10px;
  align-items: stretch;
}

.header {
  background-color: bisque;
  border-radius: var(--border-radius);
  font-size: 45px;
  grid-column: 1/3;
  grid-row: 1/1;
}

.counterButtonUp, .counterButtonDown {
  background-color: #6b9191;
  border-radius: var(--border-radius);
  color:#f3f7fc;
  font-size: 42px;
  vertical-align: middle;
}
.counterButtonUp {
  grid-column: 1/2;
  grid-row: 2/3;
}
.counterButtonDown {
  grid-column: 1/2;
  grid-row: 3/4;
}

.clickCounterIndicater {
  background-color: #ca499b;
  border-radius: var(--border-radius);
  color:#f3f7fc;
  grid-column: 2/3;
  grid-row: 2/4;
  font-size: 100px;
  font-weight: 900;
  font-style: italic;
}